.map {
  z-index: 15;
  .mapboxgl-map {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 45rem;
    width: calc(100% - 45rem);
    height: 100%;
    z-index: 10;
  }

  .pin {
    width: 30px;
    height: 30px;
    border-radius: 50% 50% 50% 0;
    background: rgba(34, 154, 250, 0.7);
    position: absolute;
    transform: rotate(-45deg);
    left: 50%;
    top: 50%;
    margin: -20px 0 0 -20px;
  }
  .pin:after {
    content: '';
    width: 14px;
    height: 14px;
    margin: 8px 0 0 8px;
    background: rgba(230, 230, 230, 0.7);
    position: absolute;
    border-radius: 50%;
  }

  .pulse {
    background: #d6d4d4;
    border-radius: 50%;
    height: 14px;
    width: 14px;
    position: absolute;
    left: 50%;
    top: 50%;
    margin: 11px 0px 0px -12px;
    transform: rotateX(55deg);
    z-index: -2;
  }
  .pulse:after {
    content: '';
    border-radius: 50%;
    height: 40px;
    width: 40px;
    position: absolute;
    margin: -13px 0 0 -13px;
    animation: pulsate 1s ease-out;
    animation-iteration-count: infinite;
    opacity: 0;
    box-shadow: 0 0 1px 2px rgba(34, 154, 250, 0.7);
    animation-delay: 1.1s;
  }

  @keyframes pulsate {
    0% {
      transform: scale(0.1, 0.1);
      opacity: 0;
    }

    50% {
      opacity: 1;
    }

    100% {
      transform: scale(1.2, 1.2);
      opacity: 0;
    }
  }
}

@media screen and (max-width: 1023px) {
  .map {
    width: 100%;
    height: 30rem;
    .mapboxgl-map {
      left: 0rem;
      width: 100%;
      height: 30rem;
    }
  }
}
