@import url(https://fonts.googleapis.com/css?family=Roboto:400,500|Roboto+Condensed);
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

body {
  box-sizing: border-box;
  font-family: 'Roboto Condensed', sans-serif; }

.font-heading {
  font-family: 'Roboto', sans-serif;
  font-weight: 400; }

.geocoderSearch {
  padding: 2rem 2rem 0 2rem; }
  .geocoderSearch input {
    z-index: 15;
    width: 100%;
    height: 3rem;
    font-size: 1.5rem;
    padding: 0.5rem; }
  .geocoderSearch div #react-geo-list {
    list-style: none;
    font-size: 1.5rem;
    position: absolute;
    width: 100%;
    background: white; }
    .geocoderSearch div #react-geo-list li {
      line-height: 4rem;
      border-bottom: 1px solid grey; }
      .geocoderSearch div #react-geo-list li:hover {
        background-color: #2060f7; }
        .geocoderSearch div #react-geo-list li:hover a {
          color: white; }
      .geocoderSearch div #react-geo-list li a {
        display: block;
        text-decoration: none;
        color: black;
        width: 100%;
        height: 100%;
        padding: 0 1rem; }

.map {
  z-index: 15; }
  .map .mapboxgl-map {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 45rem;
    width: calc(100% - 45rem);
    height: 100%;
    z-index: 10; }
  .map .pin {
    width: 30px;
    height: 30px;
    border-radius: 50% 50% 50% 0;
    background: rgba(34, 154, 250, 0.7);
    position: absolute;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    left: 50%;
    top: 50%;
    margin: -20px 0 0 -20px; }
  .map .pin:after {
    content: '';
    width: 14px;
    height: 14px;
    margin: 8px 0 0 8px;
    background: rgba(230, 230, 230, 0.7);
    position: absolute;
    border-radius: 50%; }
  .map .pulse {
    background: #d6d4d4;
    border-radius: 50%;
    height: 14px;
    width: 14px;
    position: absolute;
    left: 50%;
    top: 50%;
    margin: 11px 0px 0px -12px;
    -webkit-transform: rotateX(55deg);
            transform: rotateX(55deg);
    z-index: -2; }
  .map .pulse:after {
    content: '';
    border-radius: 50%;
    height: 40px;
    width: 40px;
    position: absolute;
    margin: -13px 0 0 -13px;
    -webkit-animation: pulsate 1s ease-out;
            animation: pulsate 1s ease-out;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    opacity: 0;
    box-shadow: 0 0 1px 2px rgba(34, 154, 250, 0.7);
    -webkit-animation-delay: 1.1s;
            animation-delay: 1.1s; }

@-webkit-keyframes pulsate {
  0% {
    -webkit-transform: scale(0.1, 0.1);
            transform: scale(0.1, 0.1);
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    -webkit-transform: scale(1.2, 1.2);
            transform: scale(1.2, 1.2);
    opacity: 0; } }

@keyframes pulsate {
  0% {
    -webkit-transform: scale(0.1, 0.1);
            transform: scale(0.1, 0.1);
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    -webkit-transform: scale(1.2, 1.2);
            transform: scale(1.2, 1.2);
    opacity: 0; } }

@media screen and (max-width: 1023px) {
  .map {
    width: 100%;
    height: 30rem; }
    .map .mapboxgl-map {
      left: 0rem;
      width: 100%;
      height: 30rem; } }

.dailyWeather {
  color: white;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 2rem;
  border-radius: 2rem;
  padding: 1.5rem; }
  .dailyWeather:nth-of-type(2) {
    background-color: #4130bb; }
  .dailyWeather:nth-of-type(3) {
    background-color: #3bddb0; }
  .dailyWeather:nth-of-type(4) {
    background-color: #f92399; }
  .dailyWeather:nth-of-type(5) {
    background-color: #fab422; }
  .dailyWeather:nth-of-type(6) {
    background-color: #229afa; }
  .dailyWeather:nth-of-type(7) {
    background-color: #db2222; }
  .dailyWeather:nth-of-type(8) {
    background-color: #2060f7; }
  .dailyWeather:nth-of-type(9) {
    background-color: #7463ee; }
  .dailyWeather__dotw {
    font-size: 2.5rem; }
  .dailyWeather__location {
    float: right;
    font-size: 2rem; }
  .dailyWeather__icon {
    margin-left: auto;
    margin-right: auto;
    width: 18rem;
    height: 9rem; }
  .dailyWeather__summary {
    font-size: 2rem;
    text-align: center;
    padding-bottom: 1.75rem; }
  .dailyWeather__low {
    font-size: 2rem;
    float: left; }
  .dailyWeather__high {
    font-size: 2rem;
    float: right; }
  .dailyWeather__clearFix {
    clear: both; }

.sidebar {
  position: relative;
  height: 100vh;
  width: 45rem;
  background: white;
  z-index: 20;
  overflow: auto; }
  .sidebar__showMap {
    float: right;
    font-size: 2rem;
    padding: 1.5rem; }
  .sidebar__header, .sidebar__empty {
    padding: 2rem;
    text-align: center; }
  .sidebar .footer {
    font-size: 1.5rem;
    text-align: center;
    padding: 1rem; }

@media screen and (max-width: 1023px) {
  .sidebar {
    width: 100%; } }

